import React, { useEffect } from "react";
import { useDispatch, useStore } from "react-redux";
import { LOGOUT_USER } from "../../redux/actions";

const Views = ({ match: { url }, history }) => {
    const dispatch = useDispatch();

    const {
        authUser: { user },
    } = useStore().getState();

    const logout = async () => {
        try {
            const response = await fetch(`${"http://localhost:8080/signout"}`, {
                method: "get",
                headers: {
                    "content-type": "application/json",
                    "Access-Control-Allow-Origin": true,
                    "Access-Control-Allow-Credentials": true,
                },
                credentials: "include",
            });
            const result = await response.json();

            localStorage.setItem("investorWalletUser", "");

            dispatch({
                type: LOGOUT_USER,
                payload: { user: "" },
            });

            history.push("/");
        } catch (e) {
            console.error(e);
        }
    };

    useEffect(() => {
        if (user?.email) {
            document.cookie =
                "connect.sid=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;"; // @todo check if it works
            // dispatch({ type: LOGOUT_USER });

            logout();
        }
    });

    return (
        <>
            <h1>logged out</h1>
        </>
    );
};

export default Views;
